import React from "react";
import { Center, HStack, Input, Button } from "@chakra-ui/react";
import { AiOutlineSearch } from "react-icons/ai";
export default function searchbar({ handleSubmit, setMeal, meal }) {
  return (
    <Center>
      <form style={{ width: "100%" }} onSubmit={(e) => handleSubmit(e)}>
        <HStack w={{ base: "95%", md: "35%" }} mx="auto">
          <Input
            value={meal}
            bg="gray.800"
            color="gray.50"
            _placeholder={{ color: "gray.400" }}
            focusBorderColor="yellow.100"
            onChange={(e) => setMeal(e.target.value)}
            placeholder="🍉 Enter a Dish"
            size="lg"
          />
          <Button
            leftIcon={<AiOutlineSearch />}
            type="submit"
            size="lg"
            bg="yellow.200"
            colorScheme="yellow"
          >
            Find
          </Button>
        </HStack>
      </form>
    </Center>
  );
}
