import { useState } from "react";
import {
  Box,
  Center,
  Text,
  VStack,
  Image,
  Button,
  useDisclosure,
  HStack,
  IconButton,
} from "@chakra-ui/react";
import { IoMdClose } from "react-icons/io";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
} from "@chakra-ui/react";
import { GrCircleInformation, GrYoutube } from "react-icons/gr";

export default function MealShowcase({ data, meal, setData }) {
  const [modalItem, setModalItem] = useState({strMeal:"",
strArea:"",strInstructions:"",strCategory:""});
  const { isOpen, onOpen, onClose } = useDisclosure();

  function openModal(item) {
    setModalItem(item);
    onOpen();
  }

  return (
    <Center bg="gray.900">
      <VStack
        w="100%"
        justify="center"
        align="center"
        spacing={{ base: "5", md: "10" }}
      >
        <HStack pl="4" bg="yellow.200" spacing="4" justify="center">
          <Text fontWeight="700" color="yellow.800">
            {meal.toUpperCase()}
          </Text>
          <IconButton
            rounded="0"
            onClick={() => setData("")}
            colorScheme="red"
            icon={<IoMdClose />}
          />
        </HStack>
        <Text color="gray.50" align="center" fontWeight="500">
          Found {data.length} Item(s)
        </Text>
        {data.map((meal) => {
          return (
            <Box w={{ base: "95%", md: "40%" }} key={meal.idMeal}>
              <VStack mx="auto" w="90%" bg="yellow.300" rounded="4" p="3">
                <Text color="yellow.800" align="center" fontWeight="700">
                  {meal.strMeal.toUpperCase()}
                </Text>
                <Image
                  w="100%"
                  rounded="4"
                  border="1px solid black"
                  src={meal.strMealThumb}
                />
                <HStack w="100%" justify="space-between">
                  <Button
                    w="50%"
                    onClick={() => openModal(meal)}
                    size="lg"
                    bg="blue.200"
                    color="gray.900"
                    leftIcon={<GrCircleInformation />}
                  >
                    Details
                  </Button>
                  <Button
                    size="lg"
                    w="50%"
                    bg="red.500"
                    onClick={() => {
                      window.open(meal.strYoutube, "_blank");
                    }}
                    color="gray.900"
                    leftIcon={<GrYoutube />}
                  >
                    Tutorial
                  </Button>
                </HStack>
              </VStack>
              <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay/>
                <ModalContent bg="yellow.300">
                  <ModalHeader color="yellow.800">{modalItem.strMeal.toUpperCase()}</ModalHeader>
                  <ModalCloseButton />
                  <ModalBody>
                    <VStack>
                    <Text fontSize="sm" fontWeight="600" color="gray.900">{modalItem.strArea.toUpperCase()} {modalItem.strCategory.toUpperCase()}</Text>
                    <Text align="center" color="yellow.100" w="100%" bg="yellow.800" fontWeight="600">COOKING INSTRUCTIONS</Text>
                    <Text w="100%" p="3" bg="blue.800" h="200px" fontSize="sm" fontWeight="600" overflow="auto" color="white">{modalItem.strInstructions.toUpperCase()}</Text>
                    </VStack>
                  </ModalBody>
                  <ModalFooter>
                    <Button colorScheme="blue" onClick={onClose}>
                      OK
                    </Button>
                  </ModalFooter>
                </ModalContent>
              </Modal>
            </Box>
          );
        })}
      </VStack>
    </Center>
  );
}
