import React from 'react'
import {HStack,Image,Text,Box} from "@chakra-ui/react"
import Logo from '../logo.png'
export default function Navbar() {
  return (
    <HStack p="3" w="100%" justify="space-between">
        <Box w="33%" align="left"><Text fontSize="2xl">🍽️</Text></Box>
        <Box w="33%" align="center"><Text fontWeight="700" fontSize="lg" color="yellow.900" display={{base:"none",md:"block"}}>Meal Database</Text></Box>
        <Box w="33%" align="right"><Image w={{base:"80%",md:"20%"}} src={Logo} /></Box>
    </HStack>
  )
}
