import {useState} from 'react'
import {Box,useToast} from "@chakra-ui/react"
import Navbar from './components/navbar'
import Searchbar from './components/searchbar'
import Showcase from "./components/showcase";
function App() {
  const toast = useToast()
  const [meal,setMeal] = useState("");
  const [data,setData] = useState("");
  const [label,setLabel] = useState("");

 async function fetchData(){
    const foodData = await fetch(`https://www.themealdb.com/api/json/v1/1/search.php?s=${meal}`);
    const res = await foodData.json();
    console.log(res.meals)
    if(!res.meals){
      toast({
        title:"Meal Not Found 😥",
        status:"error",
        duration:1000
      })
      return
    }
    toast({
      title:"Found 😋",
      status:"success",
      duration:1000
    })
    setData(res.meals)
    setLabel(meal)
    setMeal("")
  }

 function handleSubmit(e) {
    e.preventDefault()
    if(meal===""){
      toast({
        title:"Input a Meal",
        status:"error",
        duration:1000
      })
    }
    else{
      fetchData()
    }
  }
  return (
    <Box w="100%" bg="gray.900">
    <Box zIndex="100" position="fixed" top="0" pb="5" w="100%" bg="yellow.400">
      <Navbar/>
      <Searchbar handleSubmit={handleSubmit} meal={meal} setMeal={setMeal} />
    </Box>
    <Box w="100%" bg="gray.900" pt={{base:"21vh",md:"30vh"}}>
    <Showcase data={data} meal={label} setData={setData}/>
    </Box>
    </Box>
  );
}

export default App;
