import React from "react";
import { Center, SimpleGrid, Image, VStack, Text } from "@chakra-ui/react";
import FoodLogo from "./foodLogo.jpg";
export default function Hero() {
  return (
    <Center bg="gray.900" pb="20vh">
      <SimpleGrid
        spacing="5%"
        columns={{ base: "1", md: "2" }}
        w={{ base: "90%", md: "60%" }}
      >
        <Center>
          <VStack>
            <Text
              align="center"
              color="yellow.200"
              as="u"
              fontSize={{ base: "3xl", md: "5xl" }}
              fontFamily="'Carter One', cursive"
            >
              MEAL DATABASE
            </Text>
            <Text
              fontSize={{ base: "2xl", md: "4xl" }}
              fontWeight="900"
              color="gray.50"
              fontFamily="'Acme', sans-serif"
              align="center"
            >
              Free Online Food Database for Food Recipes and Cooking
              Instructions . Enter the Dish and Submit to get more Details.
            </Text>
          </VStack>
        </Center>
        <Center>
          <Image
            rounded="20"
            w={{ base: "95%", md: "90%" }}
            boxShadow="dark-lg"
            src={FoodLogo}
          />
        </Center>
      </SimpleGrid>
    </Center>
  );
}
